export default "<script context=\"module\">\n  const example = \"ResponsiveGlider\"\n</script>\n\n<script lang=\"ts\">\n  import Glider  from '..'\n  import Details from './lib/Details.svelte'\n</script>\n\n<div class=\"container\">\n  <Glider\n    class=\"glider-container\"\n    hasArrows\n    hasDots\n    slidesToShow={1}\n    slidesToScroll={1}\n    responsive={[\n      {\n        breakpoint: 775,\n        settings: {\n          slidesToShow: \"auto\",\n          slidesToScroll: \"auto\",\n          itemWidth: 150,\n          duration: 0.25\n        }\n      }\n    ]}\n  >\n    <div class=\"slide\">\n      <span>1</span>\n    </div>\n    <div class=\"slide\">\n      <span>2</span>\n    </div>\n    <div class=\"slide\">\n      <span>3</span>\n    </div>\n    <div class=\"slide\">\n      <span>4</span>\n    </div>\n    <div class=\"slide\">\n      <span>5</span>\n    </div>\n    <div class=\"slide\">\n      <span>6</span>\n    </div>\n    <div class=\"slide\">\n      <span>7</span>\n    </div>\n    <div class=\"slide\">\n      <span>8</span>\n    </div>\n    <div class=\"slide\">\n      <span>9</span>\n    </div>\n    <div class=\"slide\">\n      <span>10</span>\n    </div>\n    <div class=\"slide\">\n      <span>11</span>\n    </div>\n    <div class=\"slide\">\n      <span>12</span>\n    </div>\n  </Glider>\n  <Details {example} />\n</div>\n"